import React from "react";
import "./milan2.css";


function handleReservationClick() {
  const isLoggedIn = localStorage.getItem("isLoggedIn");

  if (isLoggedIn) {
    window.location.href = "/reservation";
  } else {
    alert("Vous devez vous connecter pour rserver");
    window.location.href = "/compte";
  }
}
function Milan2() {

  return (
    <>
      <div className='pagelist'>
        <h2>Milano</h2>
        <div className='firstart'>
          <img className="milano" src={process.env.PUBLIC_URL + 'milan.jpg'} alt="milanvoyages"></img>
          <p className='text1'>Le Duomo de Milan est un difice majestueux situ en plein cur de la ville. Cette cathdrale gothique, considre comme l'un des joyaux architecturaux de l'Italie, est un monument incontournable pour les amateurs d'art et d'histoire. Avec sa faade en marbre blanc et ses innombrables arcs-boutants, le Duomo offre une vue imprenable sur la ville et ses alentours.  l'intrieur, les visiteurs peuvent admirer de magnifiques fresques et sculptures, ainsi que l'escalier en spirale qui mne  la terrasse panoramique. Le Duomo de Milan est un symbole de la richesse culturelle et historique de la ville, et une destination incontournable pour les touristes en qute de dcouvertes inoubliables.</p>
          <div className="reservation">
            <h4>Prix: 100 par jour</h4>
            <button onClick={handleReservationClick}>Rserver</button> {/* Appel de setSelectedCity pour fixer la ville avant l'appel de handleReservationClick */}
          </div>
        </div>
        <h2>Napoli</h2>
        <div className='secondart'>
          <img className="napoli" src={process.env.PUBLIC_URL + 'naples.jpg'} alt="napolivoyages"></img>
          <p className="text2">Naples, ville situe sur la cte ouest de l'Italie, est connue pour sa riche histoire, sa cuisine dlicieuse et son architecture impressionnante. Des rues pittoresques aux places animes, Naples offre une exprience culturelle unique. Les attractions populaires incluent le muse archologique national, le Castel dell'Ovo, le Vsuve et les ruines de Pompi. La ville est galement clbre pour sa pizza napolitaine, considre comme l'une des meilleures au monde. Dcouvrez les merveilles de cette ville ensoleille et vibrante lors de votre prochain voyage en Italie.</p>
          <div className="reservation">
            <h4>Prix: 100 par jour</h4>   
            <button onClick={handleReservationClick}>Rserver</button>
          </div>
        </div>
        <h2>Roma</h2>
        <div className='thirdart'>
          <img className="roma" src={process.env.PUBLIC_URL + 'roma.jpg'} alt="romavoyages"></img>
          <p className='text3'>Rome, la ville ternelle, est un joyau historique de l'Italie. Cette ville fascinante est remplie d'art, d'architecture, de culture et de cuisine dlicieuse. Visitez les attractions lgendaires telles que le Colise, la Fontaine de Trevi, la Basilique Saint-Pierre et le Panthon. Marchez dans les rues paves de la ville et dcouvrez les boutiques de mode, les restaurants pittoresques et les cafs ensoleills. Rome est galement rpute pour sa cuisine italienne authentique, notamment les ptes fraches et les gelati. Explorez cette ville anime et passionnante pour dcouvrir pourquoi elle est une destination de choix pour les voyageurs du monde entier.</p>
          <div className="reservation">
            <h4>Prix: 100 par jour</h4>
            <button onClick={handleReservationClick}>Rserver</button>
          </div>
        </div>
      </div>
  </>)}


export default Milan2;