import React, { useState, useEffect } from 'react';
import "./voyages.css"
import axios from 'axios';

function Voyages({ email }) {
    const [voyages, setVoyages] = useState([]);
    useEffect(() => {
        // Appel  l'API pour rcuprer les voyages rservs par l'utilisateur actuel
        axios.get(`http://localhost:5000/api/inscription?email=${email}`)
            .then(response => {
                setVoyages(response.data);
            })
            .catch(error => console.error(error));
    }, [email]);

    const handleDelete = (Id) => {
        // Utiliser window.confirm() pour demander confirmation  l'utilisateur
        const confirmed = window.confirm("tes-vous sr(e) de vouloir supprimer ce voyage ?");
        if (confirmed) {
            // Appel  l'API pour supprimer le voyage correspondant  l'Id donn
            axios.delete(`http://localhost:5000/api/inscription/${Id}`, {
                headers: {
                    Authorization: `Bearer ${localStorage.getItem('token')}`
                }
            })

                .then(response => {
                    // Mise  jour de l'tat des voyages en supprimant celui qui a t supprim de la base de donnes
                    const updatedVoyages = voyages.filter(voyage => voyage.Id !== Id);
                    setVoyages(updatedVoyages);
                })
                .catch(error => console.error(error));
        }
    };
    return (
        <div className='voyage'>
            <h1>Vos voyages rservs :</h1>
            {voyages.length === 0 ? (
                <div>Vous n'avez pas encore rserv de voyage.</div>
            ) : (
                <ul>
                    {voyages.map(voyage => (
                        <li key={voyage.Id}>
                            <div>Date de dpart : {voyage.startDate}</div>
                            <div>Date de fin : {voyage.finDate}</div>
                            <div>Ville : {voyage.city}</div>
                            <div>Nombre de personnes : {voyage.numberOfPeople}</div>
                            <button onClick={() => handleDelete(voyage.userId)}>Supprimer</button>
                        </li>
                    ))}
                </ul>
            )}
        </div>
    );
}

export default Voyages;

